@use '../../sass/mixin' as *;
@use '../../sass/variables' as *;
.login_page{
    @include flex(center , flex-start);
   height: 100%;
   border-radius: 16px;
  
   
   
}
.login_left-wrap{
    height: 100%;
    width: 100%;
    @include flex (center,unset);
    flex-direction: column;
    flex: 1;
    border-radius: 16px;
}
.login_left{
    @include flex(center,center);
    height: 100%;
    width: 592px;
    padding: 82px 94px 259px;
    background-color: $bg-color;
    &__logo{
         margin-bottom: 75px;
    }
    &__form {
        @include flex (center, center)
    }
    &__account{
        font-size: 18px;
        margin-bottom: 16px;
        position: relative;
    }
    &__account:nth-child(2){
        
        margin-bottom: 12px;
    }
    &__input{
        font-size: 18px;
        width: 400px;
        height: 44px;
        padding: 8.5px 0px 8.5px 12px;
        border: 1.5px solid #D4D4D7;
        border-radius: 8px;
        
        
    }
    &__input:focus {
      outline:none;
      border: 1.5px solid blue;
    }
    &__input.error{
        border: 1.5px solid #E73F3F;
    }
    &__title{
        margin-bottom: 4px;
        display: block;
        line-height: 27px;
    }
    &__question{
   

    font-weight: 400;
    color: #E73F3F;
    font-size: 14px;
    line-height: 21px;
    margin-bottom: 19px;
    cursor: pointer;
    }
    &__button{
        width: 100%;
        @include flex(center,center);
        margin-bottom : 8px;
    }
    &__question1{
    @include flex(center,center);
    font-weight: 400;
    color: #E73F3F;
    font-size: 14px;
    line-height: 21px;
    cursor: pointer;
    }
}
.login_right{
    
   text-align: left;
    width: 848px;
    background-size:contain;
   background-position:top;
   background: cover;
   height: 100%;
   background-repeat: no-repeat;
   flex-direction: column;
   &-title{
       margin-top: 407px;
       margin-left: 454px;
       font-family: 'Nunito';
font-style: normal;
font-weight: 400;
font-size: 34px;
line-height: 51px;
/* or 150% */



/* Orange/$-Orange-500 */

color: #FF7506;
    &-sub{
        margin-left: 454px;
        font-family: 'Nunito';
font-style: normal;
font-weight: 900;
font-size: 36px;
line-height: 54px;
/* identical to box height, or 150% */



/* Orange/$-Orange-500 */

color: #FF7506;

    }
   }
}

.eye_icon{
    position: absolute;
    right: 13.66px;
    top: 60%;
    cursor: pointer;
}