@use "../../sass/mixin" as *;
.addrole
{
    &-wrap {
        margin: 0px 0px 0px 200px;
        background-color:#F7F7F7;
        height: 722px;
        padding: 24px 0px 24px 24px;
       border-bottom-left-radius: 12px;
       border-bottom-right-radius: 12px;
       
       
    }
    &-rules{
        @include flex(center,flex-start); 
      
        span {
            font-weight: 600;
            font-size: 16px;
            line-height: 24px;
            color: #535261;
            margin: 6px;
        }
      
    }
    &-checkbox{
        
            border: none !important;
            outline: 2px solid #4277FF;
            width: 15px;
            height: 15px;
        
       
       
       
      }
    &-title
    {
        font-weight: 700;
        font-size: 24px;
        line-height: 36px;
        color: #FF7506;
        margin-bottom: 16px;
    }
    &-main{
        background-color: white;
        height: 534px;
        width: 1192px;
        padding: 16px 24px;
        box-shadow: 2px 2px 8px rgba(232, 239, 244, 0.8);
        border-radius: 16px;
       
        &__top{
            font-style: normal;
                font-weight: 700;
        font-size: 20px;
            line-height: 30px;   
            color: #FF7506;
        }
        &__group{
            @include flex (unset,flex-start);
            margin-top: 12px;
           flex:  1;
            &__left{
                margin-right: 24px;
            }
            &__right{
                width: 560px;
             
             
            
              
            }
        }
        &__title{
            font-weight: 600;
            font-size: 16px;
            line-height: 24px;
            color: #282739;
            margin-bottom: 8px;
            @include flex(center,flex-start);
            img {
                margin-left : 4px;
            }
        }
        &__input{
            width: 560px;
            height: 44px;
            padding: 10px 270px 10px 12px;
            border: 1.5px solid #D4D4D7;
            box-sizing: border-box;
            border-radius: 8px;
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
            margin-bottom: 16px;

        }
        &__input1
        {
            width: 560px;
            height: 132px;
            padding: 10px 270px 10px 12px;
            border: 1.5px solid #D4D4D7;
            box-sizing: border-box;
            border-radius: 8px;
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
            margin-bottom: 16px;
           
        }
     
        &__desc{
            font-size: 14px;
            font-weight: 400;
            line-height: 21px;
            @include flex(center,flex-start);
            img {
                margin-right: 4px;
            }
        }
        &__button{
            margin:24px 396px auto 368px;
            button {
                margin-left: 32px;
            }
        }
    }
}
.addrole-main__group__righ__roles{
    background-color: #FFF2E7;
    width: 560px;
    padding: 24px;
    border-radius: 8px;
}
.mg-bottom {
    margin-bottom: 5px;
}
.mg-top {
    margin-top: 10px;
}